* {
    font-family: "Open Sans", sans-serif;
}

@-webkit-keyframes popupQueen {
    0% {
        transform: scale3d(1, 1, 1);
    }
    50% {
        transform: scale3d(1.5, 1.5, 1.5);
    }
    100% {
        transform: scale3d(1, 1, 1);
    }
}

@keyframes popupQueen {
    0% {
        transform: scale3d(1, 1, 1);
    }
    50% {
        transform: scale3d(1.5, 1.5, 1.5);
    }
    100% {
        transform: scale3d(1, 1, 1);
    }
}
.Qboard {
    position: relative;
    border-radius: 3px;
    padding: 10px;
    width: 100%;
}
.Qboard .q-array-tile {
    display: inline-block;
    margin: 2px;
    text-align: center;
    border-radius: 2px;
}
.Qboard .q-array-tile i.fa-crown {
    font-size: 25px;
    color: #212121;
}

.popupQueen {
    -webkit-animation-name: popupQueen;
            animation-name: popupQueen;
    -webkit-animation-duration: 400ms;
            animation-duration: 400ms;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-direction: normal;
            animation-direction: normal;
    transform-origin: center;
    transform-box: fill-box;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
    transition: 400ms all;
}

.Qbox {
    display: grid;
    grid-gap: 0 0;
    background-color: #212121;
    grid-template-rows: repeat(var(--Qgrid-rows), 1fr);
    grid-template-columns: repeat(var(--Qgrid-cols), 1fr);

    box-shadow: 0 0.5px 3px rgba(0, 0, 0, 0.12), 0 1px 2px rgba(0, 0, 0, 0.24);
    transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}

.Qgrid-item {
    padding-top: 100%;
    cursor: pointer;
    text-align: center;
}

.highlight {
    transition: ease-in 300ms;
    transform: scale3d(1.25, 1.25, 1.25);
    margin: 1.5rem;
}

#NQueenResults {
    width: 100%;
    height: 50vh;
    overflow: scroll;
    overflow-x: hidden;
    overflow-y: scroll;
}

.alert-success {
    background-color: #abebc6;
}

* {
    font-family: "Open Sans", sans-serif;
}
.Rboard {
    position: relative;
    border-radius: 3px;
    padding: 10px;
    background-color: #fff;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 900px) {
    .Rboard .r-array-tile {
        display: inline-block;
        margin: 1px;
        text-align: center;
        border-radius: 2px;
        padding: 6px;
    }
    .Rboard .r-array-tile i.rat {
        font-size: 20px;
    }
}

@media only screen and (min-device-width: 901px) {
    .Rboard .r-array-tile {
        display: inline-block;
        margin: 1px;
        text-align: center;
        border-radius: 2px;
        padding: 20px;
    }
    .Rboard .r-array-tile i.rat {
        font-size: 30px;
    }
}

.Rbox {
    display: grid;
    grid-gap: 0 0;
    grid-template-rows: repeat(var(--Rgrid-rows), 1fr);
    grid-template-columns: repeat(var(--Rgrid-cols), 1fr);
    box-shadow: 0 0.5px 3px rgba(0, 0, 0, 0.12), 0 1px 2px rgba(0, 0, 0, 0.24);
    transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}

.Rgrid-item {
    cursor: pointer;
    text-align: center;
}

@-webkit-keyframes highlightPath {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes highlightPath {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.highlightPath {
    -webkit-animation: highlightPath 100ms;
            animation: highlightPath 100ms;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
    transition: 100ms all;
}

.flipH {
    transform: scale(-1, 1);
    transform: scale(-1, 1);
}

.plus {
  background-color: #e7e7e7;
}

.plus-center {
  background-color: #2ed573;
}

.shadowF {
  box-shadow: 0 0.5px 3px rgba(0, 0, 0, 0.12), 0 1px 2px rgba(0, 0, 0, 0.24);
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}

:root {
  --f-grid-cols: 1;
  --f-grid-rows: 1;
}

.box-f {
  background-color: #ced6e0;
  padding: 6px;
}

.grid-f {
  display: grid;
  grid-gap: 0 0;
  border-radius: 5px;
  padding: 2px;
  grid-template-rows: repeat(1, 1fr);
  grid-template-rows: repeat(var(--f-grid-rows), 1fr);
  grid-template-columns: repeat(1, 1fr);
  grid-template-columns: repeat(var(--f-grid-cols), 1fr);
}

.node-empty {
  background-color: #212121;
}

.node-boundary {
  background-color: #57606f;
}

.node-color-1 {
  background-color: #dc143c;
}

.node-color-2 {
  background-color: #78e08f;
}

.node-color-3 {
  background-color: #273d93;
}

.node-color-4 {
  background-color: #fa983a;
}

.node-color-5 {
  background-color: #5f27cd;
}

.node-color-6 {
  background-color: #f368e0;
}

.node-color-7 {
  background-color: #fd7402;
}

.node-color-8 {
  background-color: #118403;
}

.node-color-9 {
  background-color: #ffffff;
}

.f-node {
  padding-top: 100%;
}

